    //       // this.myProfit.settle_time = filterTime(filter(param.substring(448,512)))
    //       this.countDown = setInterval(() => {
    //         this.myProfit.settle_time = this.cutDown(filter(param.substring(448,512)))
    //       }, 1000)
    //   })

      getProfit() {
      let param = {
        "jsonrpc":"2.0",
        "method":"eth_call",
        "params": [{
          "from":this.myAddress,
          "to": this.ingold_add,
          "data": "0x66d16cc3"
        },"latest"],
        "id":1
      }
      this.$http.post(this.rpc_url,param).then(res => {
        console.log('------',res)
          param = res.data.result.replace("0x","")
          this.myProfit.innumber = (filter(param.substring(0,64))/Math.pow(10,18)).toFixed(4)
          this.myProfit.outgrade = filter(param.substring(64,128))/Math.pow(10,18)
          this.myProfit.outsub = (filter(param.substring(128,192))/Math.pow(10,18)).toFixed(4)

          this.myProfit.exprofit0 = (filter(param.substring(256,320))/Math.pow(10,18)).toFixed(4)
          this.myProfit.exprofit1 = (filter(param.substring(320,384))/Math.pow(10,18)).toFixed(4)
          this.myProfit.psnumber = (filter(param.substring(384,448))/Math.pow(10,18)).toFixed(4)
          this.myProfit.pwnumber = (filter(param.substring(448,512))/Math.pow(10,18)).toFixed(4)
          this.myProfit.awnumber = (filter(param.substring(512,576))/Math.pow(10,18)).toFixed(4)
          this.myProfit.bonusprofit = (filter(param.substring(576,640))/Math.pow(10,18)).toFixed(4)
          this.myProfit.exprofit = (filter(param.substring(640,704))/Math.pow(10,18)).toFixed(4)
          this.myProfit.shareprofit = (filter(param.substring(704,768))/Math.pow(10,18)).toFixed(4)
          this.myProfit.comprofit = (filter(param.substring(768,832))/Math.pow(10,18)).toFixed(4)
          this.myProfit.jackpotprofit = (filter(param.substring(832,896))/Math.pow(10,18)).toFixed(4)
          this.myProfit.profitcum = (filter(param.substring(896,960))/Math.pow(10,18)).toFixed(4)
          this.myProfit.guessprofit = (filter(param.substring(960,1024))/Math.pow(10,18)).toFixed(4)

          this.countDown = setInterval(() => {
            this.myProfit.time = this.cutDown(filter(param.substring(192,256)))
          }, 1000)
      })


    },

    //获取燃料比例
    getProportion() {
      let param = {
        "jsonrpc":"2.0",
        "method":"eth_call",
        "params": [{
          "to": this.gas_add,
          "data": "0x1bec591d"
        },"latest"],
        "id":1
      }
      this.$http.post(this.rpc_url,param).then(res => {
        this.proportion = filter(res.data.result)
      })

    },



    //邀请码模块，数据请求
    getInvite() {
      let param = {
        "jsonrpc":"2.0",
        "method":"eth_call",
        "params": [{
          "from":this.myAddress,
          "to": this.ingold_add,
          "data": "0x0d042131"
        },"latest"],
        "id":1
      }
      this.$http.post(this.rpc_url,param).then(res => {
        param = res.data.result.replace("0x","")
        this.invite.parent_address = "0x"+param.substring(24,64)
        this.invite.child_count = filter(param.substring(64,128))
      })
    },


    //复制邀请链接
    copyLink() {
      let _this = this;
      imToken.callAPI('native.setClipboard', _this.inviteCode + '?r=' +this.myAddress)
    },



    // 入金（暂不知道是什么）
    join() {
      let parentaddress = this.parentadd.replace(/\s*/g,"")
      if(parentaddress.length != 42){
          imToken.callAPI('native.alert', "parent address error")
          return
      }
      let data = '0xf340fa01000000000000000000000000'+parentaddress.substring(2)
      let param = {
        to: this.ingold_add,
        from: this.myAddress,
        value: this.value * Math.pow(10,18),
        data: data,
        gas:1000000,
        feeCustomizable: true,
      }

      imToken.callAPI('transaction.tokenPay', param, async (err, signature) => {
        if (err) {
          imToken.callAPI('native.alert', err.message)
        } else {
          imToken.callAPI('native.alert',"success")
        }
      })
    },


    isoutm() {
      let url = 'https://ethwinner.pro1.lnkj1.com/Api/Index/isoutm'
      let param = {
        user_id: this.user_id,
      }
      this.$http.post(url,param).then(res => {
        if (res.data.status == 0) {
          this.isBusy = false
          layer.msg(response.info)
        } else {
          this.join()
        }
      })

    },

    // 购买燃料
    purchase() {
      let param = {
        to: this.gas_add,
        from: this.myAddress,
        value: this.value * Math.pow(10,18), //1个币1000000000000000000
        gas: 200000,
      }
      imToken.callAPI('transaction.tokenPay', param, async (err, signature) => {
        if (err) {
          imToken.callAPI('native.alert', err.message)
        } else {
          imToken.callAPI('native.alert',"success")
        }
      })
    },

    // 结算
    settlement() {
      let param = {
        to: this.ingold_add,
        from: this.myAddress,
        value: 0,
        gas: 1000000,
        data: '0x11da60b4',
        feeCustomizable: true,
      }
      imToken.callAPI('transaction.tokenPay', param, async (err, signature) => {
        if (err) {
          imToken.callAPI('native.alert', err.message)
        } else {
          imToken.callAPI('native.alert',"success")
        }
      })
    },

    // 提现
    cashwithdrawal() {
      this.settlement()
    },



    //截取浏览器带过来的参数，返回对象类型
    getQuery() {
      let arr = window.location.search.substring(1).split('&')
      let obj = {}
      arr.forEach((item) => {
        let keyValue = item.split('=')
        Object.assign(obj, {
          [keyValue[0]]: decodeURI(keyValue[1])
        })
      })
      window.localStorage.setItem('parentAddress',obj.r)
      if(obj.r){
        this.parentadd = obj.r;
      }

      return obj
    },


    //查询余额
    initBalance () {
      let param = {
        "jsonrpc":"2.0",
        "method":"eth_call",
        "params": [{
          "from": this.myAddress,
          "to": this.gas_add,
          "data": "0xb98b8696"
        },"latest"],
        "id":1
      }
      this.$http.post(this.rpc_url,param).then(res => {
        param = res.data.result.replace("0x","")
        this.balanceObj.ewtBalance = filter(param)/Math.pow(10,18).toFixed(4)
      })

      param = {
        "jsonrpc":"2.0",
        "method":"eth_getBalance",
        "params": [
          this.myAddress,
          "latest"
        ],
        "id":1
      }
      this.$http.post(this.rpc_url,param).then(res => {
        param = res.data.result.replace("0x","")
        this.balanceObj.ethBalance = (filter(param)/Math.pow(10,18)).toFixed(4)
      })

    },


    //初始化
    initApp() {
      if (window.ethereum) {
        window.web3 = new Web3(ethereum);
        try {
          ethereum.enable();
          if (window.imToken) {
            //调用IMtoken，获取账户
            imToken.callAPI('user.getCurrentAccount', (err, address) => {
              if (err) {
                imToken.callAPI('native.alert', err)
              } else {
                this.myAddress = address
                this.initData()
                this.getProportion()
                this.getStatistic()
                this.getVipCount()
                this.getQuery()
                this.getProfit()
                this.getMyTeam()
                this.getInvite()
                this.initBalance()

              }
            })
          } else {
            // this.myAddress = web3.eth.accounts[0]
            this.myAddress = "0x5905d0eecf814a04848e7a1bba9dec0cbc5895f1"
            this.initData()
            this.getProportion()
            this.getStatistic()
            this.getVipCount()
            this.getQuery()
            this.getProfit()
            this.getMyTeam()
            this.getInvite()
            this.initBalance()
          }
        } catch (error) {
          imToken.callAPI('native.alert', error)
        }
      } else if (window.web3) {

      }



    },

    //初始化请求
    initData () {
      let data = {
        "jsonrpc":"2.0",
        "method":"eth_call",
        "params": [{
          "to": this.ingold_add,
          "data": "0x2986c0e5"
        },"latest"],
        "id":1
      }
      this.$http.post(this.rpc_url,data).then(res => {

        //截取字符串、参数赋值
        let params = res.data.result.replace("0x","");
        this.infoObj.big_round = filter(params.substring(0,64))
        this.infoObj.smallTime = filter(params.substring(64,128))
        this.infoObj.small_sum_money = filter(params.substring(128,192))/Math.pow(10,18).toFixed(4)
        this.infoObj.bigTime = filter(params.substring(192,256))
        this.infoObj.big_money = filter(params.substring(256,320))/Math.pow(10,18).toFixed(4)
        this.timer = setInterval(() => {
          this.bigTime = this.cutDown(this.infoObj.bigTime)
        }, 1000)
        this.infoObj.round = filter(params.substring(320,384))

        this.infoObj.c_time = filterTime(filter(params.substring(384,448)))
        this.infoObj.block = filter(params.substring(448,512))
        this.infoObj.count = filter(params.substring(512,576))
        this.infoObj.winnum = filter(params.substring(576,640))/Math.pow(10,18).toFixed(4)
        this.infoObj.puteht = filter(params.substring(640,704))/Math.pow(10,18)
        this.infoObj.hash = params.substring(704,768)
        this.infoObj.number = params.substring(768,772)
      })

    }
  },
  created() {
    this.initApp()

  }
}
</script>

