   padding: 0 10px;
  }

.item-btn{
  min-width: 65px;
  min-height: 25px;
  padding:4px 23px;
  font-size: 14px;
  color: #1dabbb;
  border-radius: 5px;
  border: 1px solid #1dabbb;
}
  .parent-input{
    padding:0;
    margin-bottom:10px;
  }
  .parent-input .fuel-input-inner{
    width:100%;
  }
  .fuel-input-inner input{
    width:100%;
  }

  .tab-border{
    height:2px;
    background: #fff;
    margin:5px 0;
  }
</style>



