        //倒计时
        cutDown(end_time) {
          let date = new Date();
          let time = (date.getTime()) / 1000;
          let lag = end_time - time;
          if (lag < 0) {
            return [0,0,0,0,0,0]
          }
          let second = Math.floor(lag % 60) + '';
          let minite = Math.floor((lag / 60) % 60) + '';
          let hour = Math.floor((lag / 3600) % 24) + '';
          if (second < 10) {
            second = '0' + second.toString()
          }
          if (minite < 10) {
            minite = '0' + minite.toString()
          }
          if (hour < 10) {
            hour = '0' + hour.toString()
          }
          let arr = []
          arr.push(hour[0],hour[1],minite[0],minite[1],second[0],second[1])
          return arr
        },

        cutDownFn (end_time) {
          let date = new Date();
          let time = (date.getTime()) / 1000;
          let lag = end_time - time;
          if (lag < 0) {
            return '00:00:00'
          }
          let second = Math.floor(lag % 60) + '';
          let minite = Math.floor((lag / 60) % 60) + '';
          let hour = Math.floor((lag / 3600) % 24) + '';
          if (second < 10) {
            second = '0' + second.toString()
          }
          if (minite < 10) {
            minite = '0' + minite.toString()
          }
          if (hour < 10) {
