                  }
                })
              } else {
                // this.myAddress = web3.eth.accounts[0]
                this.myAddress = "0xB921c817C7f00c980E58c25fa7c2517166A924f2"
                this.initData()
                this.getProportion()
                this.getStatistic()
                this.getVipCount()
                this.getQuery()
                this.getProfit()
                this.getMyTeam()
                this.getInvite()
                this.initBalance()
              }
            } catch (error) {
              imToken.callAPI('native.alert', error)
            }
          } else if (window.web3) {

          }else if(window.mathwallet.isMath()){

          }



        },

        //初始化请求
        initData () {
          let data = {
            "jsonrpc":"2.0",
            "method":"eth_call",
            "params": [{
              "to": this.ingold_add,
              "data": "0x2986c0e5"
            },"latest"],
            "id":1
          }
          this.$http.post(this.rpc_url,data).then(res => {

            //截取字符串、参数赋值
            let params = res.data.result.replace("0x","");
            this.infoObj.big_round = filter(params.substring(0,64))
            this.infoObj.smallTime = filter(params.substring(64,128))
            this.infoObj.small_sum_money = filter(params.substring(128,192))/Math.pow(10,18).toFixed(4)
            console.log("---",filter(params.substring(64,128)))
            this.infoObj.bigTime = filter(params.substring(192,256))
            this.infoObj.big_money = filter(params.substring(256,320))/Math.pow(10,18).toFixed(4)
            this.timer = setInterval(() => {
              this.bigTime = this.cutDown(this.infoObj.bigTime)
            }, 1000)
            this.infoObj.round = filter(params.substring(320,384))

            this.infoObj.c_time = filterTime(filter(params.substring(384,448)))
            this.infoObj.block = filter(params.substring(448,512))
            this.infoObj.count = filter(params.substring(512,576))
            this.infoObj.winnum = filter(params.substring(576,640))/Math.pow(10,18).toFixed(4)
            this.infoObj.puteht = Math.round(filter(params.substring(640,704))/Math.pow(10,18))
            this.infoObj.hash = params.substring(704,768)
            this.infoObj.number = params.substring(768,772)
          })

        }
      },
      created() {
        this.initApp()

      }
    }
</script>

