<template>
  <div>
    <div id="page">
      <header>
        <router-link to="/" tag="div">
          <img src="../assets/img/logo.png" class="logo">
        </router-link>
        <button class="switch" @click="visibleLang=true">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-switch-black"></use>
          </svg>
          <span id="lang">{{currentLang.label}}</span>
        </button>
        <div class="locale-wrapper" v-show="visibleLang" @click="visibleLang=false">
          <div class="locale">
            <div class="locale-item" v-for="item in lang" @click="getLang(item.value)">
              <svg class="icon" aria-hidden="true">
                <use :xlink:href="item.icon"></use>
              </svg>
              <span>{{item.label}}</span>
            </div>
          </div>
        </div>
        <div class="language other">
          <div class="language-item" v-for="item in lang" @click="getLang(item.value)">
            <svg class="icon" aria-hidden="true">
              <use :xlink:href="item.icon"></use>
            </svg>
            <span>{{item.label}}</span>
          </div>
        </div>
      </header>
      <div class="title">
        <img src="../assets/img/title.png">
        <span>{{translate.day_open_win}}</span>
        <img src="../assets/img/title_c.png">
      </div>
      <div class="info">
			<span
        style="color: #FF0000;">{{type==1?translate.first_prize:type==2?translate.second_prize:type==3?translate.third_prize:''}}</span>
        <span>，{{translate.win_count}}</span>
        <span style="color: #1DABBB;">{{address.length}}</span>
        <span>{{translate.person}}</span>
      </div>
      <div class="turn">
        <p>（{{translate.di}}{{round-1}}{{translate.round}}）</p>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-img_"></use>
        </svg>
      </div>
      <ul class="content">
        <li>
          <div>{{translate.time}}：</div>
          <div>{{wintime}}</div>
        </li>
        <li>
          <div>{{translate.bonus}}：</div>
          <div>+{{winnumber}} ETH</div>
        </li>
        <li class="content-li">
          <div>{{translate.winAddress}}：</div>
          <ul>
            <li v-for="(item, key) in address" :key="key">{{item}}</li>
          </ul>
        </li>
      </ul>
      <!--        <div class="list">-->
      <!--          <div class="item" v-for="(item, key) in list" :key="key">-->
      <!--            <div class="number">{{item}}</div>-->
      <!--            <div class="content">-->
      <!--              <div class="value">+{{winnumber}} ETH</div>-->
      <!--              <div class="date">{{wintime}}</div>-->
      <!--            </div>-->
      <!--          </div>-->
      <!--        </div>-->
      <div class="back">
        <router-link to="/" tag="div">
          {{translate.back_index}}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "lottery",
    data () {
      return{
        currentLang: {
          label: '繁体中文',
          value: 'zh-tw'
        },
        lang: [
          {
            label: '繁体中文',
            value: 'zh-tw',
            icon: '#icon-img_zhongg',
            url: 'static/lang/zh-tw.json'
          }, {
            label: '日本语',
            value: 'riyu',
            icon: '#icon-img_rib',
            url: 'static/lang/riyu.json'
          }, {
            label: '한국어',
            value: 'hanyu',
            icon: '#icon-img_hang',
            url: 'static/lang/hanyu.json'
          }, {
            label: 'English',
            value: 'en-us',
            icon: '#icon-img_meig',
            url: 'static/lang/en-us.json'
          }, {
            label: 'Français',
            value: 'fayu',
            icon: '#icon-img_fag',
            url: 'static/lang/fayu.json'
          }, {
            label: 'русский',
            value: 'eluosi',
            icon: '#icon-img_els',
            url: 'static/lang/eluosi.json'
          }],
        round: 1,
        translate: {},
        visibleLang: false,
        wintime: 0,
        winnumber: 0,
        // address: ['0x5905d0eecF814A04848e7a1bba9DEC0cbC5895F9',
        //   '0x5905d0eecF814A04848e7a1bba9DEC0cbC5895F9',
        //   '0x5905d0eecF814A04848e7a1bba9DEC0cbC5895F9',
        //   '0x5905d0eecF814A04848e7a1bba9DEC0cbC5895F9',
        //   '0x5905d0eecF814A04848e7a1bba9DEC0cbC5895F9'],
        number: '',
        address: []
      }
    } ,
    methods: {
      //请求语言包
      getLang(lang) {
        let item =  this.lang.find((item) => {
          return item.value == lang
        })
        let url = item.url
        this.currentLang.label = item.label
        this.currentLang.value = item.value
        this.$http.get(url).then(res => {
          console.log(res.data)
          this.translate = res.data[0]
        })
      },

      //初始化数据请求
      getList() {
        let data = ['0xcc066df3','0x1d56c957','0xb9613e4a']
        let param = {
          "jsonrpc":"2.0",
          "method":"eth_call",
          "params": [{
            "to": this.ingold_add,
            "data": data[this.type-1]
          },"latest"],
          "id":1
        }
        this.$http.post(this.rpc_url,param).then(res => {
          param = res.data.result.replace("0x","")

          //时间
          this.wintime= filterTime(filter(param.substring(param.length-64,param.length)))
          //奖金
          this.winnumber  = filter(param.substring(param.length-128,param.length-64))/Math.pow(10,18)

          //中奖地址
          if(this.type == 1){
              let add_ = '0x'+param.substring(24,64)
              if(filter(add_) != 0){
                this.address = [add_]
              }
          }else if (this.type == 2){
               for(i=0;i<5;i++){
                 let add_ = '0x'+param.substring(i*64 + 24,(i+1)*64)
                 if(filter(add_) != 0){
                   this.address[i] = add_
                 }
               }
          }else{
              // this.address = [
              //   '0x'+param.substring(24,64),
              //   '0x'+param.substring(88,128),
              //   '0x'+param.substring(152,192),
              //   '0x'+param.substring(116,156),
              //   '0x'+param.substring(280,320),
              //   '0x'+param.substring(344,384),
              //   '0x'+param.substring(408,448),
              //   '0x'+param.substring(472,512),
              //   '0x'+param.substring(536,576),
              //   '0x'+param.substring(600,640)
              // ]
            for(i=0;i<10;i++){
              let add_ = '0x'+param.substring(i*64 + 24,(i+1)*64)
              if(filter(add_) != 0){
                this.address[i] = add_
              }
            }
          }

          //截取字符串、参数赋值
          param = res.data.result.replace("0x","");
        })
      },

    },
    created() {
      this.type = this.$route.query.type;
      this.number = this.$route.query.number;
      this.round = this.$route.query.round;
      let lang = window.localStorage.getItem('lang')
      // if(window.imToken){
        this.getList()
      // }

      if (lang) {
        this.getLang(lang)
      } else {
        this.getLang('zh-tw')
      }
    }
  }
</script>

<style scoped>
  @import '../css/lottery.css';
  @import '../css/base.css';
  .item-address{
    font-size:11px;
  }
  .content{
    width:80vw;
    margin:10px auto;
    text-align: left;
    border:1px solid #eee;
    border-radius:5px;
    padding:10px;
  }
  .content li{
    line-height: 30px;
  }
  .content-li ul{
    overflow: hidden;
    word-wrap: break-word
  }
  .content-li ul li{
    width:100%;
    line-height: 20px;
    margin:10px 0;
    word-wrap: break-word
  }
</style>



