<template>
    <div class="container">
      <div id="page">
      <header>
        <router-link to="/HelloWorld">
          <img src="../assets/img/logo.png" alt="" class="logo">
        </router-link>
        <button class="switch" @click="visibleLang=true">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-switch-black"></use>
          </svg>
          <span id="lang">{{currentLang.label}}</span>
        </button>
        <div class="locale-wrapper" v-show="visibleLang" @click="visibleLang=false">
          <div class="locale">
            <div class="locale-item" v-for="item in lang" @click="getLang(item.value)">
              <svg class="icon" aria-hidden="true">
                <use :xlink:href="item.icon"></use>
              </svg>
              <span>{{item.label}}</span>
            </div>
          </div>
        </div>
        <div class="language other">
          <div class="language-item" v-for="item in lang" @click="getLang(item.value)">
            <svg class="icon" aria-hidden="true">
              <use :xlink:href="item.icon"></use>
            </svg>
            <span>{{item.label}}</span>
          </div>
        </div>
      </header>
      <div class="title">
        <img src="../assets/img/title.png">
        <span>{{translate.draw_results}}</span>
        <img src="../assets/img/title_c.png">
      </div>
      <div class="turn">
        <p>{{translate.ten_win}}</p>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-img_"></use>
        </svg>
      </div>
      <ul class="center-info">
        <router-link :to="{path:'/history',query:{round:list-key}}" tag="li" v-for="(item, key) in list" :key="key" class="center-info-li">
          <div class="info-li-img">
            <span class="info-li-title">{{translate.di}}{{list-key}}{{translate.round}}</span>
          </div>
          <div class="info-row">
            <i class="row-title">{{translate.winnerDetails}}</i>
            <i class="iconfont icon-jiantou_yemian_xiangyou row"></i>
          </div>

        </router-link>
      </ul>
      <div class="back">
        <router-link to="/">{{translate.back_index}}</router-link>
      </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: "details",
      data () {
          return {
            currentLang: {
              label: '繁体中文',
              value: 'zh-tw'
            },
            lang: [{
              label: '繁体中文',
              value: 'zh-tw',
              icon: '#icon-img_zhongg',
              url: 'static/lang/zh-tw.json'
            }, {
              label: '日本语',
              value: 'riyu',
              icon: '#icon-img_rib',
              url: 'static/lang/riyu.json'
            }, {
              label: '한국어',
              value: 'hanyu',
              icon: '#icon-img_hang',
              url: 'static/lang/hanyu.json'
            }, {
              label: 'English',
              value: 'en-us',
              icon: '#icon-img_meig',
              url: 'static/lang/en-us.json'
            }, {
              label: 'Français',
              value: 'fayu',
              icon: '#icon-img_fag',
              url: 'static/lang/fayu.json'
            }, {
              label: 'русский',
              value: 'eluosi',
              icon: '#icon-img_els',
              url: 'static/lang/eluosi.json'
            }],
            list: 5,
            round: 0,
            translate: {},
            visibleLang: false,
          }
      },
      methods:{
          //请求语言包
        getLang(lang) {
          let item =  this.lang.find((item) => {
            return item.value == lang
          })
          let url = item.url
          this.currentLang.label = item.label
          this.currentLang.value = item.value
          this.$http.get(url).then(res => {
            this.translate = res.data[0]
          })
        },

      },
      created () {
        this.list = this.$route.query.round -1
        let lang = window.localStorage.getItem('lang')
        if (lang) {
          this.getLang(lang)
        } else {
          this.getLang('zh-tw')
        }
      }
    }
</script>

<style scoped>
@import "../css/base.css";
@import '../css/history.css';
.center-info{
  width:90vw;
  margin:10px auto;
}
  .center-info-li{
    width:90%;
    margin:0 auto;
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    text-align: left;
  }
.info-li-img{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 96px;
  height: 74px;
  text-align: center;
  background: url('../assets/img/history.png') center / 100% 100% no-repeat;
  transform: translateY(-8px);
}
  .info-li-title{
    font-size: 19px;
    font-weight: bold;
    color:#354888;
  }
  .row{
    font-size:30px;
    color: #1dabbb;
    right:5px;
  }
  .info-row{
    position:relative ;
    width:37%;
  }
  .row-title{
    color:#1dabbb;
    left:5px;
  }
  .info-row i{
   position:absolute;
    top:50%;
    transform: translate(0,-50%);
  }
.center-info li{
  width:100%;
  margin:10px 0;
  background: #fcfcfc;
  border:1px solid #eee;
  border-radius:5px;
}
</style>



