<template>
    <div class="history">
      <img src="../assets/image/logo.png" alt="" class="logo-pic" @click="$router.push('/')">

      <ul class="header">
        <li class="header-title Ycolor fontTh">{{$t('day_open_win')}}</li>
        <li>
          <img src="../assets/image/icon-close.png" alt="" class="row-down">
        </li>
      </ul>
        <ul class="main-ul" v-if="list > 0">
          <router-link :to="{path:'/recordsDetails',query:{round:list-key}}" tag="li" v-for="(item, key) in list" :key="key" class="allFlex">
            <div class="Wcolor font-Th">
              <span class="info-li-title">{{$t('di')}}{{list-key}}{{$t('round')}}</span>
            </div>
            <div class="info-row">
              <i class="row-title Wcolor">{{$t('winnerDetails')}}</i>
              <i class="iconfont Wcolor icon-close_yemian_xiangyou row"></i>
            </div>
          </router-link>
        </ul>
        <router-link to="/" tag="div" class="back-btn GreenColor">{{$t('back_index')}}</router-link>
    </div>
</template>

<script>
    export default {
        name: "HistoryDetails",
      data () {
          return{
            list: 5,
            round: 0,
            translate: {},
            visibleLang: false,
          }
      },
      created () {
        this.list = this.$route.query.round -1 > 0 ? this.$route.query.round -1 : 0
      }
    }
</script>

<style scoped>
  .history{
    min-height: 100vh;
    height:100%;
    background: url("../assets/image/bg-todaylottery.png")no-repeat;
    background-size: 100% 100%;
    padding-top:200px;
    box-sizing: border-box;
  }
  .logo-pic{
    position:absolute;
    top:30px;
    left:40px;
    width:140px;
  }
  .header-title{
    background: url("../assets/image/title-bg.png") no-repeat;
    background-position: center;
    background-size:256px 23px;
    position: relative;
    margin-bottom:20px;
  }
  .row-down{
    width:22px;
  }
  .main-ul{
    width:100vw;
    box-sizing: border-box;
    min-height: 600px;
    height: 100%;
    padding:80px 0 60px;
    background: url("../assets/image/bg-btn-alert4.png")no-repeat;
    background-size: 100% 100%;
  }
  .main-ul li{
    width:70%;
    height:90px;
    line-height: 90px;
    padding: 0 30px;
    margin:0 auto 20px;
    background: url("../assets/image/alert-bg.png");
    background-size: 100% 100%;
  }

  .back-btn{
    width:287px;
    height:95px;
    line-height: 95px;
    background: url("../assets/image/send-btn.png")no-repeat;
    background-size: 100% 100%;
    margin:40px auto;
  }
</style>



