<template>
    <div class="container">
      <div id="page">
        <header>
          <router-link to="/" tag="div">
            <img src="../assets/img/logo.png" class="logo">
          </router-link>
          <button class="switch" @click="visibleLang=true">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-switch-black"></use>
            </svg>
            <span id="lang">{{currentLang.label}}</span>
          </button>
          <div class="locale-wrapper" v-show="visibleLang" @click="visibleLang=false">
            <div class="locale">
              <div class="locale-item" v-for="item in lang" @click="getLang(item.value)">
                <svg class="icon" aria-hidden="true">
                  <use :xlink:href="item.icon"></use>
                </svg>
                <span>{{item.label}}</span>
              </div>
            </div>
          </div>
          <div class="language other">
            <div class="language-item" v-for="item in lang" @click="getLang(item.value)">
              <svg class="icon" aria-hidden="true">
                <use :xlink:href="item.icon"></use>
              </svg>
              <span>{{item.label}}</span>
            </div>
          </div>
        </header>
        <div class="list">
          <div class="item" v-for="item in profit">
            <div class="date">{{item.time}}</div>
            <div class="content">
              <div class="label">{{translate.sum_money}}</div>
              <div class="value">{{item.num}}</div>
            </div>
          </div>
        </div>
        <div class="back">
          <router-link to="/" tag="div">{{translate.back_index}}</router-link>
        </div>
      </div>
    </div>
</template>

<script>
  import Qs from 'qs'
    export default {
        name: "profit",
      data () {
          return {
            currentLang: {
              label: '繁体中文',
              value: 'zh-tw'
            },
            lang: [{
              label: '繁体中文',
              value: 'zh-tw',
              icon: '#icon-img_zhongg',
              url: 'lang/zh-tw.json'
            }, {
              label: '日本语',
              value: 'riyu',
              icon: '#icon-img_rib',
              url: 'lang/riyu.json'
            }, {
              label: '한국어',
              value: 'hanyu',
              icon: '#icon-img_hang',
              url: 'lang/hanyu.json'
            }, {
              label: 'English',
              value: 'en-us',
              icon: '#icon-img_meig',
              url: 'lang/en-us.json'
            }, {
              label: 'Français',
              value: 'fayu',
              icon: '#icon-img_fag',
              url: 'lang/fayu.json'
            }, {
              label: 'русский',
              value: 'eluosi',
              icon: '#icon-img_els',
              url: 'lang/eluosi.json'
            }],
            profit: [],
            translate: {},
            visibleLang: false,
            myAddress: ''
          }
      },
      methods: {

          //请求语言包
        getLang(lang) {
          let item =  this.lang.find((item) => {
            return item.value === lang
          })
          let url = item.url
          this.currentLang.label = item.label
          this.currentLang.value = item.value
          this.$http.get(url).then(res => {
            this.translate = res.data[0]
          })
        },

        //请求数据
        getProfit() {
          let url = 'https://ethwinner.pro1.lnkj1.com/Api/Index/userDayWin'
          let param = {
            user_id: this.user_id,
          }
          this.$http.post(url,param).then(res => {
            this.profit = res.data.data
          })
        },
        login() {
          let url = 'https://ethwinner.pro1.lnkj1.com/Api/Index/doLogin'
          let address = window.localStorage.getItem('parentAddress')
          let param = {
            address: this.myAddress,
            parent_address: address
          }
          this.$http.post(url,param).then(res => {
            this.user_id = res.data.data.user_id
            this.getProfit()
          })

        },

        //初始化，获取账户
        initApp() {
          if (window.ethereum) {
            window.web3 = new Web3(ethereum);
            try {
              ethereum.enable();
              web3.eth.defaultAccount = web3.eth.accounts[0]
              this.myAddress = web3.eth.accounts[0]
              this.login()
            } catch (error) {

            }
          } else if (window.web3) {

          }
        }
      },
      created() {
          this.initApp()
        let lang = window.localStorage.getItem('lang')
        if (lang) {
          this.getLang(lang)
        } else {
          this.getLang('zh-tw')
        }
      }
    }
</script>

<style scoped>
  @import '../css/profit.css';
  /*@import '../css/layer.css';*/
  @import '../css/base.css';
  .item-left{
    width:50%;
    text-align: center;

  }
  .item-right{
    width:50%;
    text-align: center;
  }
  .item-info{
    color: #d0d0d0;
  }
  .item-title{
    color:#e2c36e;
    line-height:50px !important;
  }
  .darkBlue{
    background: #233181;
  }
  .lightBlue{
    background: #233163;
  }
  .container{
    height:100vh;
    background: #382b8b;
  }
</style>



