import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/home'
import Bets from '@/components/Bets'
import ToDayLottery from '@/components/ToDayLottery'
import HistoryDetails from '@/components/HistoryDetails'
import RecordsDetails from '@/components/RecordsDetails'
import lottery from '@/components/lottery'
import profit from '@/components/profit'
import history from '@/components/history'
import betting from '@/components/betting'
import details from '@/components/details'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/helloWorld',
      name: 'helloWorld',
      component: HelloWorld
    },
    {
      path: '/lottery',
      name: 'lottery',
      component: lottery
    },
    {
      path: '/profit',
      name: 'profit',
      component: profit
    },
    {
      path: '/history',
      name: 'history',
      component: history
    },
    {
      path: '/betting',
      name: 'betting',
      component: betting
    },
    {
      path: '/details',
      name:'details',
      component: details
    },
    {
      path: '/bets',
      name:'bets',
      component: Bets
    },
    {
      path: '/toDayLottery',
      name:'toDayLottery',
      component: ToDayLottery
    },
    {
      path: '/historyDetails',
      name:'historyDetails',
      component: HistoryDetails
    },
    {
      path: '/recordsDetails',
      name:'recordsDetails',
      component: RecordsDetails
    }

  ]
})



