<template>
    <div class="lang">
      <div class="lang-btn allFlex" @click="show = !show">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="icon"></use>
        </svg>
        <span class="BlueColor font-Th">{{langText}}</span>
        <img src="../../assets/image/lang-down.png" alt="" class="down-pic" :class="show ? 'check-icon' : ''">
      </div>

      <ul class="lang-box" :class="show ? 'lang-show-box' : 'lang-hide-box'">
        <li v-for="(item, key) in langArr" :key="key" class="allFlex" @click="changLang(item)">
          <svg class="icon" aria-hidden="true">
            <use :xlink:href="item.icon"></use>
          </svg>
          <span id="lang">{{item.label}}</span>
        </li>
      </ul>
    </div>

</template>

<script>
    export default {
        name: "changeLang",
      data () {
          return{
            langArr: [
              {
                label: '繁体中文',
                value: 'Tr',
                icon: '#icon-img_zhongg',
              }, {
                label: '日本语',
                value: 'Ja',
                icon: '#icon-img_rib',
              }, {
                label: '한국어',
                value: 'Ko',
                icon: '#icon-img_hang',
              }, {
                label: 'English',
                value: 'En',
                icon: '#icon-img_meig',
              }, {
                label: 'Français',
                value: 'Fr',
                icon: '#icon-img_fag',
              }, {
                label: 'русский',
                value: 'Ru',
                icon: '#icon-img_els',
              }],
            show:false,
            langText: '繁体中文',
            icon: '#icon-img_zhongg'
          }
      },
      methods: {
        changLang (item) {
          this.langText = item.label;
          this.icon = item.icon
          this.show = !this.show
          this.$i18n.locale = item.value
          window.sessionStorage.setItem('lang', item.value)
        },
        getLang () {
          let obj = this.langArr.find(item =>{
            return item.value === this.$i18n.locale
          })
          this.langText = obj.label
          this.icon = obj.icon
        }
      },
      created () {
          this.getLang()
      }
    }
</script>

<style scoped>
  .lang{
    position:absolute;
    top:44px;
    right:33px;
    z-index: 9999;
  }
  .lang-btn{
    width:230px;
    height:60px;
    line-height: 60px;
    padding:0 14px;
    align-items: center;
    border:2px solid #fff;
    border-radius:10px;
  }
.down-pic{
  width:18px;
  height:12px;
  transition: ease 0.3s;
}
  .check-icon{
    transform: rotate(180deg);
  }

  /*下拉*/
  .icon{
    width:30px;
    height:30px;
    fill: currentColor;
    overflow: hidden;
    vertical-align: middle;
  }
  .lang-box{
    background: #fff;
    border-radius:5px;
    width:200px;
    position: absolute;
    right:0;
    top:80px;
    overflow: hidden;
    transition:height 0.3s ease;
    will-change: height;
  }
  .lang-box li{
    height:60px;
    line-height: 60px;
    padding:0 20px;
    align-items: center;
    border-bottom:1px solid #f6f6f6;
  }
  .lang-show-box{
    height:360px;
  }
  .lang-hide-box{
    height:0;
  }
</style>



