<template>
    <div class="container">
      <img src="../assets/image/logo.png" alt="" class="logo-pic" @click="$router.push('/')">

      <ul class="header">
        <li class="header-title Ycolor fontTh">{{$t('day_open_win')}}</li>
        <li class="header-title-btn">
          <span class="RedColor font-Th">{{type==1?$t('first_prize'):type==2?$t('second_prize'):type==3?$t('third_prize'):''}}</span>
          <span class="Wcolor font-Th">, {{$t('win_count')}}</span>
          <span class="RedColor font-Th">{{address.length}}</span>
          <span class="Wcolor font-Th">{{$t('person')}}</span>
        </li>
        <li>
          <img src="../assets/image/icon-close.png" alt="" class="row-down">
        </li>
      </ul>

     <div class="main padding">
       <div class="main-info">
         <ul >
           <li class="allFlex column">
             <span class="Ycolor font-Th">{{$t('time')}}:</span>
             <span class="li-bg Wcolor font-Th">{{wintime}}</span>
           </li>

           <li class="allFlex column">
             <span class="Ycolor font-Th">{{$t('bonus')}}:</span>
             <span class="li-bg Wcolor font-Th">+{{winnumber}} ETH</span>
           </li>

           <li class="allFlex column">
             <span class="Ycolor font-Th">{{$t('winAddress')}}:</span>
             <div class="li-bg li-address Wcolor" v-for="(item, key) in address" :key="key">
               <span  class="font-Ts">{{item}}</span>
             </div>
           </li>
         </ul>
       </div>
       <router-link to="/" tag="div" class="back-btn GreenColor">{{$t('back_index')}}</router-link>
     </div>
    </div>
</template>

<script>
    export default {
        name: "ToDayLottery",
      data () {
        return{
          round: 1,
          translate: {},
          visibleLang: false,
          wintime: 0,
          winnumber: 0,
          number: '',
          address: []
        }
      },
      methods: {

        //初始化数据请求
        getList() {
          let data = ['0xcc066df3','0x1d56c957','0xb9613e4a']
          let param = {
            "jsonrpc":"2.0",
            "method":"eth_call",
            "params": [{
              "to": this.ingold_add,
              "data": data[this.type-1]
            },"latest"],
            "id":1
          }
          this.$http.post(this.rpc_url,param).then(res => {
            param = res.data.result.replace("0x","")
            //时间
            this.wintime= filterTime(filter(param.substring(param.length-64,param.length)))

            //奖金
            this.winnumber  = filter(param.substring(param.length-128,param.length-64))/Math.pow(10,18)

            //中奖地址
            if(this.type == 1){
              let add_ = '0x'+param.substring(24,64)
              if(filter(add_) != 0){
                this.address = [add_.slice(0,10)+"***"+add_.slice(add_.length-10,add_.length)]
              }
            }else if (this.type == 2){
              for(i=0;i<5;i++){
                let add_ = '0x'+param.substring(i*64 + 24,(i+1)*64)
                if(filter(add_) != 0){
                  this.address[i] = add_.slice(0,10)+"***"+add_.slice(add_.length-10,add_.length)
                }
              }
            }else{
              // this.address = [
              //   '0x'+param.substring(24,64),
              //   '0x'+param.substring(88,128),
              //   '0x'+param.substring(152,192),
              //   '0x'+param.substring(116,156),
              //   '0x'+param.substring(280,320),
              //   '0x'+param.substring(344,384),
              //   '0x'+param.substring(408,448),
              //   '0x'+param.substring(472,512),
              //   '0x'+param.substring(536,576),
              //   '0x'+param.substring(600,640)
              // ]
              for(i=0;i<10;i++){
                let add_ = '0x'+param.substring(i*64 + 24,(i+1)*64)
                if(filter(add_) != 0){
                  this.address[i] = add_.slice(0,10)+"***"+add_.slice(add_.length-10,add_.length)
                }
              }
            }

            //截取字符串、参数赋值
            param = res.data.result.replace("0x","");
          })
        },

      },
      created() {
        this.type = this.$route.query.type;
        this.number = this.$route.query.number;
        this.round = this.$route.query.round;
        // if(window.imToken){
        this.getList()
        // }
      }
    }
</script>

<style scoped>
  .container{
    min-height: 100vh;
    height:100%;
    background: url("../assets/image/bg-todaylottery.png")no-repeat;
    background-size: 100% 100%;
    padding-top:200px;
  }
  .logo-pic{
    position:absolute;
    top:30px;
    left:40px;
    width:140px;
  }
  .header-title{
    background: url("../assets/image/title-bg.png") no-repeat;
    background-position: center;
    background-size:256px 23px;
    position: relative;
  }
  .header-title-btn{
    width:400px;
    height:70px;
    line-height: 70px;
    background: rgb(15,58,95);
    margin:40px auto;
  }
  .row-down{
    width:22px;
  }
  .main-info{
    height:470px;
    background: url("../assets/image/todaylottery-border.png")no-repeat;
    background-size: 100% 100%;
    margin:40px 0;
    padding:50px 60px;

  }
  .main-info ul {
    height:430px;
    margin:auto 0;
    overflow-y: scroll;
  }
  .main-info ul li{
    flex:1;
    text-align: left;
  }
  .li-bg{
    background: url("../assets/image/lottery-li-bg.png")no-repeat;
    background-size: 100% 100%;
    padding:14px 20px;
    margin:14px 0;
  }

  .back-btn{
    width:287px;
    height:95px;
    line-height: 95px;
    background: url("../assets/image/send-btn.png")no-repeat;
    background-size: 100% 100%;
    margin:20px auto;
  }
  .li-address{
    margin-bottom:14px;
  }
  .li-address span{
    word-break: break-all;
    overflow: hidden;
    overflow-wrap: break-word;
  }
</style>



